.popup__container {
  background-color: white;
  padding: 25px 22px;
  position: relative;
  border-radius: 10px;
  box-shadow: 0 0 25px rgba(0, 0, 0, .15);
}

@media screen and (min-width: 1280px) {
  .popup__container {
    width: 100%;
    max-width: 358px;
    padding: 35px 36px 37px;
  }
}

