.infotooltip__container {
  box-sizing: border-box;
  text-align: center;
  margin: 0 auto;
  padding: 0;
  width: 100%;
  max-width: 280px;
  height: 308px;
}

@media screen and (min-width: 768px) {
  .infotooltip__container{
    max-width: 430px;
    height: 100%;
    max-height: 330px;
  }
}